.card-container{
    width: 100%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}
.card-box{ 
    width: 95%;
    margin: 2%;
    padding: 1% 1% 3% 1%;
    box-sizing: border-box;
    border-radius: 10px;
}
.card-heading{
    padding: 20px;
    text-align: center;
    letter-spacing: .8px;
}
.card-heading h2{
    color: #2EE59D;
    text-transform: uppercase;
    font-size: 22px;
}

@media(max-width:720px) {
    .card-heading h2{
        color: #2EE59D;
        text-transform: uppercase;
        font-size: 20px;
    }
    
    .card-box{ 
        width: 95%;
        margin: 2%;
        padding: 1% 1% 3% 1%;
        box-sizing: border-box;
        border-radius: 4px;
    }
}